@import "Desktop.css" screen and (min-width: 768px);

@media screen and (min-width: 768px) and (max-width: 1765px) {
    .sidebar {
        position: fixed;
        font: normal normal 500 1.125rem/0.625rem Guardians;
        text-align: left;
        color: var(--diviBlue);
        border: var(--diviSky);
        top: 3.125rem;
        left: 3.125rem;
        width: 13.2%;
    }

    .sidebar hr {
        width: 19.3125rem;
        top: 7.3125rem;
        margin-left: -1.875rem;
        opacity: 0.50;
    }

    .sidebar img {
        margin-top: 1.6875rem;
        top: 2rem;
        left: 3.625rem;
        transform: scale(0.50);
        float: left;
    }
}
