
div.sidebar-container {
    position: fixed;
    background-color: white;
    width:  19.2%;
    height: 100%;
    margin-top: -4.35rem;
}

.navbar-container {
    margin-top: 4.625rem;
}

div hr.navbarline {
    position: fixed;
    width: 74%;
    margin-left: 24%;
    border-top: 0.0625rem solid var(--diviBorder);
    opacity: 0.5;
}

nav {
    color: var(--diviGrey);
    font: normal normal 700 1.35rem/1.63rem Lato;
    letter-spacing: 0.075rem;
    position: relative;
    margin-left: 62.5%;
    text-align: left;
}

.navbarLink {
    border: none;
    padding: 0.625rem 1.84375rem 0.5625rem 1.84375rem;
    color: var(--diviGrey);
}

.active {
    border-top: 0.0625rem solid rgba(112, 112, 112, 0.5);;
    border-right: 0.0625rem solid rgba(112, 112, 112, 0.5);
    border-left: 0.0625rem solid rgba(112, 112, 112, 0.5);
}

a {
    text-decoration: none;
}

Nav a:hover {
    color: var(--diviBlue);
}

.sidebar {
    position: fixed;
    font: normal normal 500 1.125rem/0.625rem Guardians;
    text-align: left;
    color: var(--diviBlue);
    border: var(--diviSky);
    top: 3.125rem;
    left: 3.125rem;
    width: 13.2%;
}

div.vertical {
    position: relative;
    margin-top: 2.4375rem;
    float: right;
    width: 0.0625rem;
    height: 90%;
    background: rgba(112, 112, 112, 0.5);
}

.sidebar hr {
    width: 19.3125rem;
    top: 7.3125rem;
    margin-left: -1.875rem;
    opacity: 0.50;
}

.sidebar img {
    margin-top: 1.6875rem;
    top: 9rem;
    left: 3.625rem;
}

#headerDivetus {
    color: var(--diviBlue);
    border: none;
}

